import React, { useEffect, useRef } from "react";
import clever from "../assets/images/clever.png";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

export const Blog: React.FC = () => {
  const containerRef = useRef<HTMLDivElement | null>(null);
  const columnsRef = useRef<HTMLDivElement[]>([]);

  useEffect(() => {
    if (!containerRef.current) return;

    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: containerRef.current,
        start: "top 80%",
        toggleActions: "play none none none",
      },
    });

    //      
    tl.fromTo(
      containerRef.current,
      { opacity: 0, y: 50 },
      { opacity: 1, y: 0, duration: 1, ease: "power2.out" },
    )
      .fromTo(
        columnsRef.current,
        { opacity: 0, y: 30 },
        { opacity: 1, y: 0, duration: 0.8, ease: "power2.out", stagger: 0 },
        "-=0.8", //    
      )
      .fromTo(
        ".bottom-image img",
        { opacity: 0, scale: 0.8 },
        { opacity: 1, scale: 1, duration: 1, ease: "power2.out" },
        "-=0.8", //   
      );
  }, []);

  return (
    <div className="page" id="page-3" ref={containerRef}>
      <div className="center-top-text">
        <h2>    </h2>
        <p>  Connect-hub  </p>
      </div>
      <div className="columns-container">
        {[
          {
            title: " ",
            text: "  ,     .",
          },
          {
            title: "  ",
            text: " ,      .",
          },
          {
            title: "",
            text: "      .",
          },
        ].map((item, index) => (
          <div
            className="column"
            key={index}
            ref={(el) => {
              if (el) columnsRef.current[index] = el;
            }}
          >
            <h3>{item.title}</h3>
            <p>{item.text}</p>
          </div>
        ))}
      </div>
      <div className="bottom-image">
        <img src={clever} alt="clever" />
      </div>
    </div>
  );
};
