import React, { useEffect, useState } from "react";
import { useForm as useReactHookForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { useForm as useFormspree, ValidationError } from "@formspree/react";
import styles from "./FormComponent.module.scss";
import ReCAPTCHA from "react-google-recaptcha";

//    Zod
const schema = z.object({
  role: z.enum(["webmaster", "partner"], {
    errorMap: () => ({ message: " " }),
  }),
  name: z
    .string()
    .min(1, " ")
    .regex(/^[A-Za-z--\s]+$/, " "),
  email: z.string().email(" email"),
  phone: z
    .string()
    .regex(/^\+?\d+$/, "       +"),
  telegram: z.string().regex(/^[A-Za-z0-9_]+$/, "   "),
  business: z.string().min(1, "  "),
  website: z.string().url(" "),
  consent1: z.boolean(),
  consent2: z
    .boolean()
    .refine(
      (val) => val,
      "      ",
    ),
});

export const FormComponent = () => {
  const [captchaToken, setCaptchaToken] = useState<string | null>(null);
  const {
    register,
    handleSubmit,
    formState: { errors },
    setValue,
    watch,
    reset,
  } = useReactHookForm({
    resolver: zodResolver(schema),
    defaultValues: { role: "webmaster" },
  });

  const [state, formspreeSubmit] = useFormspree("xjkyyoaz");

  const selectedRole = watch("role");

  //    
  function onChangeCaptcha(token: string | null) {
    setCaptchaToken(token);
  }

  //   
  const onSubmit = async (data: any) => {
    if (!captchaToken) {
      alert(" !");
      return;
    }

    const formData = { ...data, captchaToken }; //   
    try {
      await formspreeSubmit(formData);
      alert("  ");
      reset();
    } catch (err) {
      alert("   ");
    }

    setCaptchaToken(null); //    
  };

  useEffect(() => {
    document.body.style.background = "#121212";
    return () => {
      document.body.style.background = "";
    };
  }, []);

  return (
    <div className={styles.root}>
      <h1> </h1>

      {state.succeeded && (
        <p className={styles.successMessage}>  !</p>
      )}

      <form onSubmit={handleSubmit(onSubmit)}>
        {/*   */}
        <div className={styles.inputGroup}>
          <div className={styles.roleSelector}>
            <div
              className={`${styles.roleOption} ${selectedRole === "webmaster" ? styles.active : ""}`}
              onClick={() => setValue("role", "webmaster")}
            >
              
            </div>
            <div className={styles.separator}></div>
            <div
              className={`${styles.roleOption} ${selectedRole === "partner" ? styles.active : ""}`}
              onClick={() => setValue("role", "partner")}
            >
              
            </div>
          </div>
          {errors.role && (
            <div className={styles.errorMessage}>{errors.role.message}</div>
          )}
        </div>

        {/*   */}
        <div className={styles.inputGroup}>
          <input
            type="text"
            {...register("name")}
            className={styles.inputField}
            placeholder=""
          />
          {errors.name && (
            <div className={styles.errorMessage}>{errors.name.message}</div>
          )}
        </div>

        <div className={styles.inputGroup}>
          <input
            type="email"
            {...register("email")}
            className={styles.inputField}
            placeholder="E-mail"
          />
          <ValidationError prefix="Email" field="email" errors={state.errors} />
          {errors.email && (
            <div className={styles.errorMessage}>{errors.email.message}</div>
          )}
        </div>

        <div className={styles.inputGroup}>
          <input
            type="tel"
            {...register("phone")}
            className={styles.inputField}
            placeholder=" "
          />
          {errors.phone && (
            <div className={styles.errorMessage}>{errors.phone.message}</div>
          )}
        </div>

        <div className={styles.inputGroup}>
          <input
            type="text"
            {...register("telegram")}
            className={styles.inputField}
            placeholder="Telegram"
          />
          {errors.telegram && (
            <div className={styles.errorMessage}>{errors.telegram.message}</div>
          )}
        </div>

        <div className={styles.inputGroup}>
          <input
            type="text"
            {...register("business")}
            className={styles.inputField}
            placeholder="    ?"
          />
          {errors.business && (
            <div className={styles.errorMessage}>{errors.business.message}</div>
          )}
        </div>

        <div className={styles.inputGroup}>
          <input
            type="url"
            {...register("website")}
            className={styles.inputField}
            placeholder="   "
          />
          {errors.website && (
            <div className={styles.errorMessage}>{errors.website.message}</div>
          )}
        </div>

        {/*  */}
        <div className={styles.consentSection}>
          <div className={styles.checkboxContainer}>
            <input
              type="checkbox"
              {...register("consent1")}
              id="consent1"
              className={styles.checkbox}
            />
            <label
              htmlFor="consent1"
              className={`${styles.consentText} ${styles.longText}`}
            >
                      <br /> 
               SMS  email
            </label>
          </div>
          {errors.consent1 && (
            <div className={styles.errorMessage}>{errors.consent1.message}</div>
          )}

          <div className={styles.checkboxContainer}>
            <input
              type="checkbox"
              {...register("consent2")}
              id="consent2"
              className={styles.checkbox}
            />
            <label htmlFor="consent2" className={styles.consentText}>
                     
            </label>
          </div>
          {errors.consent2 && (
            <div className={styles.errorMessage}>{errors.consent2.message}</div>
          )}
        </div>

        {/*  */}
        <ReCAPTCHA
          sitekey="6LeSb_8qAAAAAJQGKFNt1DNf2KKlTrrSqe-Z6iVK"
          onChange={onChangeCaptcha}
        />

        {/*   */}
        <button
          type="submit"
          className={styles.submitButton}
          disabled={state.submitting}
        >
           
        </button>
      </form>
    </div>
  );
};
